import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-message',
  templateUrl: './message.component.html',
  styleUrls: ['./message.component.scss']
})
export class MessageComponent implements OnInit {

  @Input() title: string;
  @Input() subTitle: string;
  @Input() tinyTitle: string;
  @Input() message: string;
  @Input() imageUrl: string;
  @Input() iconName: string;
  @Input() imagePositionRight: boolean = false;
  @Input() messageReply: boolean = false;
  @Input() messageMultiLine: boolean = false;
  @Input() dateTimeInput: Date;
  @Input() badge: string;
  @Input() square: boolean = false;
  @Input() smallImage: boolean = false;
  @Input() color: string = null; // default dark fonts etc for white background - if false, then white is used
  @Input() focus: boolean = true;
  @Input() timestamp;

  avatarSlot: string = "start";
  replyPadding: string = "";
  dateTime: Date = new Date();

  constructor() { 
    
  }

  ngOnInit() {
    if(this.imagePositionRight === true){
      this.avatarSlot = "end";
    } 

    this.dateTime = this.dateTimeInput;
  }

}
