import { Injectable } from '@angular/core';
import { MenuItemModel } from '../models/MenuItemModel';

@Injectable({
  providedIn: 'root'
})
export class MenuService {

  loggedInItems1: MenuItemModel[];
  loggedInItems2: MenuItemModel[];
  loggedOutItems: MenuItemModel[];
  homePageItems: MenuItemModel[];

  loginItem: MenuItemModel;

  showTopbarMenu = true;

  constructor() {

      this.homePageItems = [
          { title: "Home", component: "Home", icon:"home", route:"home",  src: "assets/icon/menu/home.svg" }
      ];

      this.loginItem = { title: "Login / Register", component: "Login", icon: "log-in",  route:"/login",  src: "" };
      
      this.loggedInItems1 = [
          { title: "Notifications", component: "Notifications", icon: "notifications", route:"notifications", src: "assets/icon/menu/notification.svg" },
          { title: "Inbox", component: "Inbox", icon: "mail", route:"/inbox",  src: "assets/icon/menu/inbox.svg"},
          { title: "Post advert", component: "Post", icon: "cloud-upload", route:"post",  src: "assets/icon/menu/post-advert.svg"}
      ];

      this.loggedInItems2 = [
          { title: "My adverts", component: "MyAdverts", icon: "document", route:"my-adverts",  src: "assets/icon/menu/my-advert.svg" },
          { title: "My favourites", component: "Favourites", icon: "heart", route:"favourites",  src: "assets/icon/menu/like.svg"},
      ];

      this.loggedOutItems = [
      ];
  }
}
