import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { AdvertClient, AdvertEntity, MessageClient } from '../clients/bonobogive.client';

@Injectable({
    providedIn: "root"
})
export class AdvertService {
    showFooter: boolean = true;
    constructor(private advertClient: AdvertClient) {
      
    }

    getAdvert(paritionKey: string, sortKey: string): Observable<AdvertEntity> {
        return  this.advertClient.get(paritionKey, sortKey);
    }

}
