import { CommonModule } from '@angular/common';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { BrowserModule } from '@angular/platform-browser';
import { NavigationEnd, NavigationStart, RouteReuseStrategy } from '@angular/router';
import Auth from '@aws-amplify/auth';
import { Camera } from '@ionic-native/camera/ngx';
import { Geolocation } from "@ionic-native/geolocation/ngx";
import { Keyboard } from '@ionic-native/keyboard/ngx';
import { NativeStorage } from '@ionic-native/native-storage/ngx';
import { SocialSharing } from '@ionic-native/social-sharing/ngx';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { IonicModule, IonicRouteStrategy } from '@ionic/angular';
import { NgProgressModule } from '@ngx-progressbar/core';
import { NgProgressHttpModule } from '@ngx-progressbar/http';
import { NgProgressRouterModule } from '@ngx-progressbar/router';
import { AmplifyAngularModule, AmplifyModules, AmplifyService } from 'aws-amplify-angular';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { API_BASE_URL } from './clients/bonobogive.client';
import { ComponentsModule } from './components/components.module';
import { RateComponent } from './components/rate/rate.component';
import { FocusDirective } from './directives/focus.directive';
import { BonoboGiveApiBaseUrl, BonobogiveWebsocketUrl } from './environments/environment-host.service.provider';
import { PipesModule } from './pipes/pipes.module';
import { AuthInterceptor } from './services/auth.interceptor';
import { WEBSOCKET_BASE_URL } from './services/websocketnotification.service';


@NgModule({
  declarations: [AppComponent, FocusDirective],
  entryComponents: [RateComponent],
  imports: [
    BrowserModule,
    IonicModule.forRoot({mode: 'md'}),
    AppRoutingModule,
    ComponentsModule,
    PipesModule,
    FormsModule,
    ReactiveFormsModule,
    CommonModule,
    HttpClientModule,
    NgProgressModule,
    NgProgressRouterModule.withConfig({
      startEvents: [NavigationStart],
      completeEvents: [NavigationEnd],
      delay: 0

      //id: 'router-progressbar'
    }),
    NgProgressHttpModule,
    AmplifyAngularModule,
   ],
  providers: [
    {
      provide: AmplifyService,
      useFactory:  () => {
        return AmplifyModules({
          Auth
        });
      }
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: AuthInterceptor,
      multi: true
    },
    { provide: API_BASE_URL, useFactory: BonoboGiveApiBaseUrl },    
    { provide: WEBSOCKET_BASE_URL, useFactory: BonobogiveWebsocketUrl },    
    StatusBar,
    SplashScreen,
    Camera,
    Keyboard,
    SocialSharing,
    NativeStorage,
    Geolocation,
  
    { provide: RouteReuseStrategy, useClass: IonicRouteStrategy }
  ],
  bootstrap: [AppComponent]
})
export class AppModule {}
