import { Injectable } from '@angular/core';

import { ActivatedRouteSnapshot, CanActivate, RouterStateSnapshot, Router } from '@angular/router';
import { AuthService } from '../services/auth.service';
import { AmplifyService } from 'aws-amplify-angular';
import { Auth } from 'aws-amplify';

@Injectable({

  providedIn: 'root'

})

export class LoggedInRouteGuard implements CanActivate {

  constructor(private authService: AuthService, private router: Router) { }

  async canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {

    console.log("entered LoggedInRouteGuard");

    if (this.authService.user) {
      console.log(this.authService.user);
      console.log("LoggedInRouteGuard: already true;");
      return true;
    }

    let isAuthenticated = await this.authService.isAuthenticated();
    if(!isAuthenticated) return false;
    
    let user = await this.authService.loadUser();
    this.authService.user = user;
    if(this.authService.user) return true;
    return false;

  }

}