/// <reference path="../../../../node_modules/@types/googlemaps/index.d.ts" />
import { Component, OnInit } from '@angular/core';
import { } from '@google/maps';
import {  ViewChild, ElementRef, Input } from "@angular/core";
@Component({
  selector: 'app-map',
  templateUrl: './map.component.html',
  styleUrls: ['./map.component.scss']
})
export class MapComponent implements OnInit {
  map: any;
  errors: string = "No errors recorded...";
  mapStatus: string = "Map is not ready";
  constructor() { }
    @Input() lng: number;
    @Input() lat: number;
    @Input() radius: string;
    @Input() disableNavigation: boolean = false;
    @Input() draggable: boolean = true;
    @Input() disableDefaultUI: boolean = false;
    @Input() randomOffset: boolean = true;
  ngOnInit() {
    this.loadMap();
  }
  @ViewChild("map", {read: ElementRef, static: true}) mapElement: ElementRef;

  loadMap() {

    if(this.randomOffset){
      this.lat = this.lat + ((Math.random() > 0.4 ? 0.001 : -0.009) + Math.random() * 0.008);
      this.lng = this.lng + ((Math.random() > 0.4 ? 0.001 : -0.009) + Math.random() * 0.008);
    }

    let latLng = new google.maps.LatLng(this.lat, this.lng);
    let mapOptions = {
      center: latLng,
      zoom: 12,
      mapTypeId: google.maps.MapTypeId.ROADMAP,
      disableNavigation: this.disableNavigation,
      draggable: false,
      disableDefaultUI: this.disableDefaultUI

    }

    console.log("lat: " + this.lat + " lng: " + this.lng);
    this.map = new google.maps.Map(this.mapElement.nativeElement, mapOptions);
    console.log(this.mapElement.nativeElement);
    var radius = 1000;
    var cityCircle = new google.maps.Circle({
      strokeColor: "#FF0000",
      strokeOpacity: 0.8,
      strokeWeight: 2,
      fillColor: "#FF0000",
      fillOpacity: 0.35,
      map: this.map,
      center: latLng,
      radius: radius
    });
  }
}
