import { Pipe, PipeTransform } from '@angular/core';
import { AdvertService } from '../services/advert.service';
import { AdvertEntity } from '../clients/bonobogive.client';

@Pipe({
  name: 'advertExpiry'
})
export class AdvertExpiryPipe implements PipeTransform {

  constructor(public advertService: AdvertService){

  }

  transform(value: AdvertEntity, args?: any): any {
    var today = new Date();
    var createdDate = new Date(value.dateTimeUtcCreated);
    var expiryDate =new Date(createdDate);
    expiryDate.setDate(expiryDate.getDate() + 30);

    var diff = Math.abs(expiryDate.getTime() - today.getTime());
    var diffDays = Math.ceil(diff / (1000 * 3600 * 24)); 
    return diffDays;
  }

}
