
import { Injectable } from "@angular/core";
import { IonInfiniteScroll } from "@ionic/angular";
import { AdvertClient, AdvertEntity, LocationTypeEnum, SearchRequestModel, SortByEnum } from "../clients/bonobogive.client";
import { GeoService } from "./geo.service";

@Injectable({
  providedIn: 'root'
})
export class SearchService {

  searchResults: AdvertEntity[] = [];
  searchSpinner = false;
  noMoreResults = false;
  searchPageSearchRequest: SearchRequestModel;
  error = false;

  constructor(
    private geoService: GeoService,
    private advertClient: AdvertClient
  ) {

    this.init();
  }

  async init() {
    console.log("search init");
    this.refreshSearchParameters();
    this.refreshSearchResults();

    try {
      await this.geoService.refreshCurrentDevicePosition();

      let result = await this.geoService.findAddressByCoords(this.geoService.latestDevicePosition.coords.latitude, this.geoService.latestDevicePosition.coords.longitude)

      // fix - this will force getLatLngFromPostcode to run because of the onchange event
      this.searchPageSearchRequest.postcode = this.geoService.extractPostcode(result.address_components);
    }
    catch (e) {
      console.log(e);
    }

  }

  // resets all search settings
  refreshSearchParameters() {
    this.noMoreResults = false;
    this.searchPageSearchRequest = new SearchRequestModel();

    this.searchPageSearchRequest.searchQuery = undefined;
    this.searchPageSearchRequest.lat = null;
    this.searchPageSearchRequest.lng = null;

    this.searchPageSearchRequest.locationType = LocationTypeEnum.Postcode;

    this.searchPageSearchRequest.postcode = undefined;
    this.searchPageSearchRequest.distanceMiles = 50;
    this.searchPageSearchRequest.sortBy = SortByEnum.NearestFirst;
    this.searchPageSearchRequest.categoryCodes = [];
    this.searchPageSearchRequest.categoryCodes.push("All");
  }

  // keeps filters
  refreshSearchResults() {
    this.searchPageSearchRequest.size = 10;
    this.searchPageSearchRequest.from = 0;
    this.noMoreResults = false;
    this.searchResults = [];
  }

  async search(refresher, infiniteScroll) {
    this.error = false;
    try {
      this.searchSpinner = true;

      if (!this.searchPageSearchRequest.lat || !!this.searchPageSearchRequest.lng) {
        await this.geoService.refreshCurrentDevicePosition();
        this.searchPageSearchRequest.lat = this.geoService.latestDevicePosition.coords.latitude;
        this.searchPageSearchRequest.lng = this.geoService.latestDevicePosition.coords.longitude;
      }
    }

    catch (e) {
      console.log(e);
    }

    try {

      this.advertClient.searchGet(
        this.searchPageSearchRequest.searchQuery,
        this.searchPageSearchRequest.distanceMiles,
        this.searchPageSearchRequest.categoryCodes,
        this.searchPageSearchRequest.postcode,
        this.searchPageSearchRequest.lat,
        this.searchPageSearchRequest.lng,
        this.searchPageSearchRequest.sortBy,
        this.searchPageSearchRequest.locationType,
        this.searchPageSearchRequest.from,
        this.searchPageSearchRequest.size
      ).subscribe(res => {
        this.searchResults.push(...res);

        if (!res || res.length === 0) {
          this.noMoreResults = true;
        }

      }, error => {
        this.error = true;
        console.log(error);
      })
        .add(() => {
          this.searchSpinner = false;

          if (refresher) {
            refresher.target.complete();
          }

          if (infiniteScroll) {
            infiniteScroll.target.complete();
          }

        });
    }
    catch (e) {
      console.log(e);
    }

  }


}
