import { Injectable, InjectionToken, Inject } from '@angular/core';
import { Subject } from 'rxjs';
import { WebsocketService } from './websocket.service';
import { MessageEntity, MessageTypeEnum } from '../clients/bonobogive.client';
import { NavController, ToastController } from '@ionic/angular';

@Injectable({
  providedIn: 'root'
})
export class WebSocketNotificationService {

  public messages: Subject<MessageEntity>;
  public newNotifictions = false;

  constructor(
    private wsService: WebsocketService,
    @Inject(WEBSOCKET_BASE_URL) private websocketUrl: string,
    private navCtrl: NavController,
    private toastController: ToastController) {

  }

  close() {
    this.wsService.close();
    this.messages = null;
  }

  connect(userId: string) {
    this.messages = null;

    console.log(`Websocket url: ${this.websocketUrl}`);

    this.messages = <Subject<MessageEntity>>this.wsService.connect(`${this.websocketUrl}?userId=${userId}`).map(
      (response: MessageEvent): any => {

        let jsonResult = JSON.parse(response.data);

        let result: MessageEntity = new MessageEntity();
        result.text = jsonResult.Text;
        result.redirectUrl = jsonResult.RedirectUrl;
        result.dateTimeUtcCreated = jsonResult.DateTimeUtcCreated;
        result.messageType = jsonResult.MessageType;
        console.log("New message event response", result);
        this.newNotifictions = true;
        return result;
      }
    );
    this.handleNotifications();
  }


  handleNotifications() {
    this.messages.subscribe(async msg => {
      console.log("New notification", msg);

      let toast = await this.toastController.create({
        message: this.notificationMessage(msg),
        duration: 3000,
        position: "top",
        buttons: [
          {
            text: 'open',
            icon: 'star',
            handler: () => {
              console.log('toast link opened');
              this.navCtrl.navigateForward(msg.redirectUrl);
              // this.newNotifictions = false;
            }
          }
        ]

      });

      await toast.present();
    });
  }

  private notificationMessage(message: MessageEntity): string {
    switch (message.messageType) {
      case MessageTypeEnum.Private: {
        return "You have a new message"
      }
      case MessageTypeEnum.Comment: {
        return "You have a new comment"
      }
      case MessageTypeEnum.Notification: {
        return "You have a new notification"
      }
      default: {
        return "You have a new notification";
      }
    }
  }


}

export const WEBSOCKET_BASE_URL = new InjectionToken<string>('WEBSOCKET_BASE_URL');