import { Pipe, PipeTransform } from '@angular/core';
import { AdvertEntity, CategoryCodeEnum, ImageModel } from '../clients/bonobogive.client';

@Pipe({
  name: 'advertImageUrl'
})
export class AdvertImageUrlPipe implements PipeTransform {

  transform(category: CategoryCodeEnum): string {
    return this.getAdvertCategoryImage(category);
  }

  getAdvertCategoryImage(category: CategoryCodeEnum): string {

    var url = "";
    switch (category) {
      case CategoryCodeEnum.First: {
        url = "assets/images/categories/FirstComeFirstServed.svg";
        break;
      }
      case CategoryCodeEnum.All: {
        url = "assets/images/categories/all.svg";
        break;
      }
      case CategoryCodeEnum.Fashion: {
        url = "assets/images/categories/FashionAndAccessories.svg";
        break;
      }
      case CategoryCodeEnum.Baby: {
        url = "assets/images/categories/BabyAndChild.svg";
        break;
      }
      case CategoryCodeEnum.Sport: {
        url = "assets/images/categories/SportAndLeisure.svg";
        break;
      }
      case CategoryCodeEnum.Free: {
        url = "assets/images/categories/FreeService.svg";
        break;
      }
      case CategoryCodeEnum.Home: {
        url = "assets/images/categories/HomeAndGarden.svg";
        break;
      }
      case CategoryCodeEnum.Media: {
        url = "assets/images/categories/MovieAndBook.svg";
        break;
      }
      case CategoryCodeEnum.Pet: {
        url = "assets/images/categories/PetAccessories.svg";
        break;
      }
      case CategoryCodeEnum.Motor: {
        url = "assets/images/categories/MotorsAndAccessories.svg";
        break;
      }
      default: {
        url = "assets/images/no-image.png";
        break;
      }
    }
    
    return url;
  }

}
