import { Injectable } from '@angular/core';

import { CognitoUser } from '@aws-amplify/auth';
import { Auth } from 'aws-amplify';
import { AuthGuardService } from './auth.guard.service';
import { UserModel, RatingClient, EntityTypeEnum, AverageRatingEntity, ImageModel } from '../clients/bonobogive.client';
import { AmplifyService } from 'aws-amplify-angular';
import { AuthState } from 'aws-amplify-angular/dist/src/providers';
import { WebSocketNotificationService } from './websocketnotification.service';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  private authState: AuthState;
  averageRating: AverageRatingEntity;
  constructor(public authGuardService: AuthGuardService,
    public amplify: AmplifyService, private ratingClient: RatingClient, private webSocketNotificationService: WebSocketNotificationService) {


      this.isAuthenticated().then(async (res) => {
        if(!res) return;
        this.user = await this.loadUser();
        this.webSocketNotificationService.connect(this.user.uid);
      });

    this.amplify.authState().subscribe(async res => {
      this.authStateResult(res);
    });

  }

  async isAuthenticated(): Promise<boolean> {
    try {
      await await Auth.currentAuthenticatedUser();
      return true;
    }
    catch (e) {
      return false;
    }
  }


  async authStateResult(authState: AuthState): Promise<boolean> {
    this.authState = authState;
    if (!this.authState.user || this.authState.state !== 'signedIn') {
      this.webSocketNotificationService.messages = null;
      this.user = null;
      this.averageRating = null;
      return;
    };

    this.user = await this.loadUser()
    // connect web socket
    this.webSocketNotificationService.connect(this.user.uid);
    this.getRating(this.user.uid);
    return this.user ? true : false;
  }



  async loadUser(): Promise<UserModel> {

    var res = await Auth.userAttributes(this.authState.user);

    let user = new UserModel();
    user.jwtToken = this.authState.user.signInUserSession.accessToken.jwtToken;
    user.username = this.authState.user.username;

    for (let att of res) {
      var name = att.getName();
      if (name === "sub")
        user.uid = att.getValue();

      if (name === "email_verified")
        user.emailVerified = att.getValue() === "true" ? true : false;

      if (name === "given_name")
        user.givenName = att.getValue();

      if (name === "family_name")
        user.familyName = att.getValue();

      if (name === "email")
        user.email = att.getValue();

      if (name === "picture") {
        user.pictureUrl = att.getValue();
      }
    }

    user.name = `${user.givenName} ${user.familyName}`;

    return user;

  }

  getRating(userId: string) {
    this.ratingClient.average(userId, EntityTypeEnum.Rating).subscribe(res => {
      this.averageRating = res;
    }, err => console.log(err));
  }

  async deleteAccount() {
    console.log("Deleting account");
  }

  async changeEmailAddress(emailAddress: string): Promise<string> {
    const user = await Auth.currentAuthenticatedUser();
    var result = await Auth.updateUserAttributes(user, { email: emailAddress });
    console.log("Changed email");
    return result;
  }

  async changePassword(newPassword: string, oldPassword: string): Promise<boolean> {
    try {
      const user = await Auth.currentAuthenticatedUser();
      await Auth.changePassword(user, oldPassword, newPassword);
      console.log("password changed");
      return true;
    }
    catch (e) {
      console.log(e);
      return false;
    }

  }


  private _user: UserModel;
  get user() {
    return this._user;

  }
  set user(value: UserModel) {
    this._user = value;
  }

  get state() {
    return null;
  }

  async signOut() {
    try {
      var result = await Auth.signOut({ global: true });
      this.webSocketNotificationService.close();
      console.log(result);
      console.log("user signed out");
    }
    catch (e) {
      console.log("Failed to sign user out");
    }

  }

  async saveNames(forename: string, surname: string): Promise<string> {
    const user = await Auth.currentAuthenticatedUser();

    console.log("saving name, current user", user);

    var result = await Auth.updateUserAttributes(user, { given_name: forename, family_name: surname });
    console.log(result);
    console.log("Changed user attributes", await Auth.userAttributes(user));
    return result;
  }

  async saveProfilePicture(url: string): Promise<string> {
    const user = await Auth.currentAuthenticatedUser();
    var result = await Auth.updateUserAttributes(user, { picture: url });
    return result;
  }

  async resetPassword(email: string, password: string, verificationCode: string) {
    return await Auth.forgotPasswordSubmit(email, verificationCode, password);
  }

  async forgotPassword(username: string) {
    return await Auth.forgotPassword(username);
  }


  async register(username: string, email: string, firstName: string, surname: string, password: string) {

    var result = await Auth.signUp({
      "username": username,
      "password": password,
      attributes: {
        "email": email,
        "family_name": surname,
        "given_name": firstName
      }
    });

    console.log("Sign up result", result);
    // var result: CognitoUser = await Auth.signUp()
  }

  async confirmSignUp(username: string, code: string) {

    try {
      var result = await Auth.confirmSignUp(username, code, {
        // Optional. Force user confirmation irrespective of existing alias. By default set to True.
        forceAliasCreation: true

      });
      console.log(result);
    }
    catch (e) {
      console.log(e);
    }
  }


  async resendSignUp(username: string): Promise<void> {

    try {
      await Auth.resendSignUp(username);
      console.log('code resent successfully');
    }
    catch (e) {
      console.log(e);
      throw e;
    }
  }

  async signIn(username: string, password: string): Promise<boolean> {
    try {
      var result: CognitoUser = await Auth.signIn({ username: username, password: password, validationData: {} });

      console.log(result);
      console.log("user signed in");
      return true;
    }
    catch (e) {
      console.log("Failed to sign user in");
      return false;
    }
  }

  signUpConfig = {
    header: 'Welcome!',
    defaultCountryCode: '44',
    hideAllDefaults: true,
    hiddenDefaults: [
      'phone_number'
    ],
    signUpFields: [
      {
        label: 'Password',
        key: 'password',
        required: true,
        displayOrder: 3,
        type: 'password',
      },
      {
        label: 'Email',
        key: 'email',
        required: true,
        displayOrder: 2,
        type: 'email',
      },
      {
        label: 'Family name',
        key: 'family_name',
        required: true,
        displayOrder: 2,
        type: 'string',
      },
      {
        label: 'Given name',
        key: 'given_name',
        required: true,
        displayOrder: 2,
        type: 'string',
      },
      {
        label: 'Username',
        key: 'username',
        required: true,
        displayOrder: 2,
        type: 'string',
      }
    ]
  };

}
