import { HttpClient, HttpHeaders, HttpRequest } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { CategoryCodeEnum } from '../clients/bonobogive.client';
import { AuthService } from './auth.service';

@Injectable({
  providedIn: 'root'
})
export class ImageService {

  private http: HttpClient;
  constructor(public auth: AuthService, @Inject(HttpClient) http: HttpClient) { 
    this.http = http;
  }


  uploadFileToS3(image: File, presignedUrl: string) {

    const headers = new HttpHeaders({'Content-Type': image.type});
    const req = new HttpRequest(
    'PUT',
    presignedUrl,
    image,
    {
      headers: headers,
      reportProgress: true, //This is required for track upload process
    });
    return this.http.request(req);
  }




  public async filesToImages(files: File[]) : Promise<HTMLImageElement[]>{
    let results: HTMLImageElement[] = [];
    for(let file of files){
      results.push(await this.fileToImage(file));
    }
    return results;
  }


  public fileToImage(file: File): Promise<HTMLImageElement> {
    return new Promise<HTMLImageElement>(resolve => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onloadend = async (e) => {
            var base64 = reader.result.toString();
            var img = await this.base64ToImage(base64);
            resolve(img);
        };
    });
}


resizeImage(img: HTMLImageElement, maxPixels: number, mimeType: string): Promise<HTMLImageElement> {

  // create an off-screen canvas
  let canvas = document.createElement('canvas');
  let ctx = canvas.getContext('2d');

  // set its dimension to target size
  let dimensions = this.resizeDimensions(maxPixels, img.width, img.height);
  canvas.width = dimensions[0];
  canvas.height = dimensions[1];

  // draw source image into the off-screen canvas:
  ctx.drawImage(img, 0, 0, dimensions.width, dimensions.height, 0, 0, canvas.width, canvas.height);

  // encode image to data-uri with base64 version of compressed image
  let base64 = canvas.toDataURL(mimeType);
  return this.base64ToImage(base64);
}

public getMimeTypeFromBase64(base64: string): string {
  if (!base64) { return null; }
  return base64.split("data:")[1].split(";")[0];
}

imageToBase64(img: HTMLImageElement, mimeType: string) : string {
  var canvas = document.createElement('canvas')
  var ctx = canvas.getContext('2d')

  canvas.width = img.width
  canvas.height = img.height
  ctx.drawImage(img, 0, 0)
  return canvas.toDataURL(mimeType);
}

async handleImageSelect(e): Promise<HTMLImageElement> {
  console.log("handleFileSelect", e);
  var files: File[] = e.target.files;
  if (files.length < 1) {
      return;
  }

  return await this.fileToImage(files[0]);
}

private async rotateImage(image: HTMLImageElement, antiClockwise: boolean, mimeType: string): Promise<HTMLImageElement> {
  var canvas = document.createElement('canvas');
  var ctx = canvas.getContext('2d');
  canvas.width = image.width;
  canvas.height = image.height;
  ctx.translate(canvas.width / 2, canvas.height / 2);
  ctx.imageSmoothingEnabled = false;
  let rotateValue = antiClockwise ? -90 : 90;
  ctx.rotate(rotateValue * (Math.PI / 180));
  ctx.translate(-canvas.width / 2, -canvas.height / 2);
  ctx.drawImage(image, 0, 0);
  var base64 = canvas.toDataURL(mimeType);
  var result = await this.base64ToImage(base64);
  return result;
}

private resizeDimensions(maxPixels: number, originalWidth: number, originalHeight: number): ImageDimensions {
  var newWidth: number = 0;
  var newHeight: number = 0;
  var percentage: number = 0;
  if (originalWidth === originalHeight) {
      newWidth = maxPixels;
      newHeight = maxPixels;
  } else if (originalWidth > originalHeight) {
      newWidth = maxPixels;
      percentage = (newWidth / originalWidth) * 100;
      newHeight = (percentage / 100) * originalHeight;
  }
  else {
      newHeight = maxPixels;
      percentage = (newHeight / originalHeight) * 100;
      newWidth = (percentage / 100) * originalWidth;
  }

  return {
      height : newHeight,
      width : newWidth
  }
}

base64ToImage(base64: string): Promise<HTMLImageElement> {
  return new Promise<HTMLImageElement>(resolve => {
      var image = new Image();
      image.src = base64;
      image.onload = (e) => {
          resolve(image);
      };
  });
}



  public imageCategoryUrl(category: CategoryCodeEnum): string {
    switch (category) {
      case CategoryCodeEnum.First: {
        return "assets/images/categories/FirstComeFirstServed.svg";
      }
      case CategoryCodeEnum.All: {
        return "assets/images/categories/all.svg";
      }
      case CategoryCodeEnum.Fashion: {
        return "assets/images/categories/FashionAndAccessories.svg";
      }
      case CategoryCodeEnum.Baby: {
        return "assets/images/categories/BabyAndChild.svg";
      }
      case CategoryCodeEnum.Sport: {
        return "assets/images/categories/SportAndLeisure.svg";
      }
      case CategoryCodeEnum.Free: {
        return "assets/images/categories/FreeService.svg";
      }
      case CategoryCodeEnum.Home: {
        return "assets/images/categories/HomeAndGarden.svg";
      }
      case CategoryCodeEnum.Media: {
        return "assets/images/categories/MovieAndBook.svg";
      }
      case CategoryCodeEnum.Pet: {
        return "assets/images/categories/PetAccessories.svg";
      }
      case CategoryCodeEnum.Motor: {
        return "assets/images/categories/MotorsAndAccessories.svg";
      }
      default: {
        return "assets/images/no-image.png";
      }

    }

  }

}

export class ImageDimensions {
  width: number;
  height: number;
}