import { Component, OnInit } from '@angular/core';


import { ViewChild, ViewChildren, Input, Renderer2 } from '@angular/core';
import { NavController, ModalController, LoadingController, AlertController } from "@ionic/angular";

import { AuthService } from "../../services/auth.service";

import { AdvertService } from "../../services/advert.service";
import { ImageService } from '../../services/image.service';
import { AdvertEntity, UserModel,  StatusEnum, MessageEntity, MessageClient, MessageResponse, CreateMessageRequestModel, ChatClient, CreateChatModel, EntityTypeEnum, MessageTypeEnum } from '../../clients/bonobogive.client';


@Component({
  selector: 'app-comments',
  templateUrl: './comments.component.html',
  styleUrls: ['./comments.component.scss']
})
export class CommentsComponent implements OnInit {
  ngOnInit(): void {

  }
  text: string;
  avatarImageSize: string = "3em";

  @ViewChild("commentinput") commentinput;


  @Input() advert: AdvertEntity;
  @Input() user: UserModel;
  @Input() advertComments: MessageResponse[] = [];
  @Input() commentUsers: UserModel[] = [];
  @Input() showReply: boolean;

  @ViewChildren('replyInputs') replyInputs: any;

  StatusEnum = StatusEnum;

  constructor(public navController: NavController,
    public auth: AuthService,

    public modalCtrl: ModalController,
    public advertService: AdvertService,
    public imageService: ImageService,
    private loadingController: LoadingController,
    public alertController: AlertController,
    private messageClient: MessageClient,
    private chatClient: ChatClient) {



  }




  async createConversation(userId: string) {
    const loading = await this.loadingController.create({
      spinner: "dots"
    });
    await loading.present();

    var request = new CreateChatModel();
    request.createdByUserId = this.auth.user.uid;
    request.entityPrimaryKey = this.advert.primaryKey;
    request.entityType = EntityTypeEnum.Advert;
    request.userIds = [this.auth.user.uid, userId];
    // check conversation doesn't already exist

    // if it doesn't, create chat

    this.chatClient.createChat(request).subscribe(async res=>{


      // navigate to chat page
      this.navController.navigateRoot("chat/" + res.primaryKey);


    }).add(async ()=>{
      await loading.dismiss();
    });

  
  }

  // calculateImageUrl(images :IImageModel[], size : number){
  // if(!images) return "";
  //   var foundImage = images.sort(x=>x.order).find(x => x.filePath.endsWith(`_${size}.jpg`));
  //   if(foundImage) return foundImage.url;
  //   return "";
  // }

  async presentHelpAlert() {
    const alert = await this.alertController.create({
      message: `<strong>Reply</strong> 
      <p>Leave a public response</p> 
      <strong>Arrange pickup</strong> 
      <p>Open a private conversation to arrange a pickup</p>`,
      buttons: ['Close']
    });

    await alert.present();
  }

  showCommentHelp() {
    this.presentHelpAlert();
  }

  openUserProfile(userId: string) {
    this.navController.navigateForward("user-profile/" + userId);
  }

  async postReply(entityPrimaryKey: string, originalComment: MessageEntity, replyContent: string) {

    var commentSubmittingLoader = this.loadingController.create({
      message: 'Please wait...',
      translucent: true
    });

    var loading = await commentSubmittingLoader;
    var messageCreateRequest = new CreateMessageRequestModel();
    messageCreateRequest.text = replyContent;
    messageCreateRequest.parentFullSlug = originalComment.fullSlug;
    messageCreateRequest.entityPrimaryKey = entityPrimaryKey;
    messageCreateRequest.userId = this.auth.user.uid;
    messageCreateRequest.messageType = MessageTypeEnum.Comment;
    messageCreateRequest.entityType = EntityTypeEnum.Advert;
    loading.present();
    this.messageClient.post(messageCreateRequest).subscribe(res => {
      // if(this.replyInputs._results && this.replyInputs._results[0])
      // this.replyInputs._results[0].value = "";#

      var messageResponse = new MessageResponse();
      messageResponse.user = this.auth.user;
      messageResponse.message = res;

      this.advertComments.push(messageResponse);
      this.advertComments.sort((a, b) => (a.message.fullSlug > b.message.fullSlug) ? 1 : -1);


      console.log(`New comment id: ${res}`);
      loading.dismiss();
    }, err => {
      console.log("error submitting comment", err);
      loading.dismiss();
    }, () => {

    });
  }



  ngAfterViewInit() {

  }

  async presentReplyPopup(commentResponse: MessageResponse) {
    const alert = await this.alertController.create({
      header: `Reply to ${commentResponse.user.givenName}`,
      inputs: [
        {
          name: 'reply',
          type: 'text',
          placeholder: 'Reply...'
        },
      ],
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          cssClass: 'secondary',
          handler: () => {
            console.log('Confirm Cancel');
          }
        }, {
          text: 'Send reply',
          handler: async (data) => {
            if (!data) return;
            console.log(data["reply"]);

            await this.postReply(this.advert.primaryKey, commentResponse.message, data["reply"]);

            console.log('Reply sent');
          }
        }
      ]
    });

    await alert.present();
  }
}
