import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoggedInRouteGuard } from './resolvers/AuthenticatedGuard.resolver';

const routes: Routes = [
  { path: '', redirectTo: 'search', pathMatch: 'full' },
  { path: 'home', redirectTo: 'search', pathMatch: 'full' },
 // { path: 'home', loadChildren: './pages/home/home.module#HomePageModule' },
  { path: 'about', loadChildren: () => import('./pages/about/about.module').then(m => m.AboutPageModule) },
  { path: 'admin', loadChildren: () => import('./pages/admin/admin.module').then(m => m.AdminPageModule), canActivate: [LoggedInRouteGuard] },
  { path: 'advert/:id', loadChildren: () => import('./pages/advert/advert.module').then(m => m.AdvertPageModule), data : { preload: true} },
  { path: 'inbox', loadChildren: () => import('./pages/inbox/inbox.module').then(m => m.InboxPageModule), canActivate: [LoggedInRouteGuard] },
  { path: 'chat/:chatId', loadChildren: () => import('./pages/inbox/chat/chat.module').then(m => m.ChatPageModule), canActivate: [LoggedInRouteGuard] },
  { path: 'introduction', loadChildren: () => import('./pages/introduction/introduction.module').then(m => m.IntroductionPageModule) },
  { path: 'login', loadChildren: () => import('./pages/login/login.module').then(m => m.LoginPageModule) },
  { path: 'login/:showcode', loadChildren: () => import('./pages/login/login.module').then(m => m.LoginPageModule) },
  { path: 'my-adverts', loadChildren: () => import('./pages/my-adverts/my-adverts.module').then(m => m.MyAdvertsPageModule), canActivate: [LoggedInRouteGuard] },
  { path: 'notifications', loadChildren: () => import('./pages/notifications/notifications.module').then(m => m.NotificationsPageModule), canActivate: [LoggedInRouteGuard] },
  { path: 'post/:id', loadChildren: () => import('./pages/post/post.module').then(m => m.PostPageModule), canActivate: [LoggedInRouteGuard]  },
  { path: 'post', loadChildren: () => import('./pages/post/post.module').then(m => m.PostPageModule), canActivate: [LoggedInRouteGuard] },
  { path: 'privacy', loadChildren: () => import('./pages/about/privacy/privacy.module').then(m => m.PrivacyPageModule) },
  { path: 'terms-and-conditions', loadChildren: () => import('./pages/about/terms-and-conditions/terms-and-conditions.module').then(m => m.TermsAndConditionsPageModule) },
  { path: 'register', loadChildren: () => import('./pages/account/register/register.module').then(m => m.RegisterPageModule) },
  { path: 'user-profile/:id', loadChildren: () => import('./pages/user-profile/user-profile.module').then(m => m.UserProfilePageModule) },
  { path: 'profile', loadChildren: () => import('./pages/account/profile/profile.module').then(m => m.ProfilePageModule), canActivate: [LoggedInRouteGuard] },
  { path: 'favourites', loadChildren: () => import('./pages/favourites/favourites.module').then(m => m.FavouritesPageModule), canActivate: [LoggedInRouteGuard] },
  { path: 'search/:showKeyboard', loadChildren: () => import('./pages/search/search.module').then(m => m.SearchPageModule) },
  { path: 'search', loadChildren: () => import('./pages/search/search.module').then(m => m.SearchPageModule) },
  { path: 'forgot-password', loadChildren: () => import('./pages/forgot-password/forgot-password.module').then(m => m.ForgotPasswordPageModule) },
  { path: 'rating', loadChildren: () => import('./pages/rating/rating.module').then(m => m.RatingPageModule), canActivate: [LoggedInRouteGuard] },
  { path: 'advert-status/:id', loadChildren: () => import('./pages/advert-status/advert-status.module').then(m => m.AdvertStatusPageModule), canActivate: [LoggedInRouteGuard] },
  { path: 'report/:id', loadChildren: () => import('./pages/report/report.module').then(m => m.ReportPageModule), canActivate: [LoggedInRouteGuard] },
  { path: 'verification', loadChildren: './pages/verification/verification.module#VerificationPageModule' },

 
  // { path: 'post', loadChildren: './pages/new/post/post.module#PostPageModule' },
  // { path: 'new-post', loadChildren: './pages/new/post/post.module#PostPageModule' },
  // { path: 'new-my-adverts', loadChildren: './pages/new/my-adverts/my-adverts.module#MyAdvertsPageModule' },
   // { path: 'authenticate', loadChildren: () => import('./pages/new/authenticate/authenticate.module').then(m => m.AuthenticatePageModule) }


];
@NgModule({
  providers: [],
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {}
