import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'distance'
})
export class DistancePipe implements PipeTransform {

  kilometerToMile(kilometer: number): number {
    if(!kilometer) return 0;
    var result = kilometer / 1.60934;

    if(!isNaN(result))
    return parseFloat(result.toFixed(1));
  }

  mileToKilometer(mile: number): number {
    if(!mile) return 0;
    var result = (mile / 0.621371);
    if(!isNaN(result))
    return parseFloat(result.toFixed(1));
  }


  transform(value: number, arg?: string): any {

    if (arg === "mileToKm") {
      return this.mileToKilometer(value);
    }
    return this.kilometerToMile(value);
  }

}
