import { Component, OnInit } from '@angular/core';

import { MenuService } from '../../services/menu.service';
import { AuthService } from '../../services/auth.service';
import { AlertController, ToastController, MenuController, NavController, LoadingController, Platform } from '@ionic/angular';
import { Router } from '@angular/router';
import { MenuItemModel } from '../../models/MenuItemModel';
import { UserModel, RatingClient, EntityTypeEnum } from '../../clients/bonobogive.client';


@Component({
    selector: 'app-menu',
    templateUrl: './menu.component.html',
    styleUrls: ['./menu.component.scss']
})
export class MenuComponent implements OnInit {
    loggedInItems1: MenuItemModel[];
    loggedInItems2: MenuItemModel[];
    loggedOutItems: MenuItemModel[];
    homePageItems: MenuItemModel[];
    userLoggedIn = false;
    loginItem: MenuItemModel;
    showBadge: boolean = true;
    user: UserModel;
    userImageProfileUrl : string = "assets/images/logos/large.png";
    loadingAverageRating = false;
    constructor(public itemsService: MenuService,

        public auth: AuthService,
        public alertCtrl: AlertController,
        private toastCtrl: ToastController,
        public menuCtrl: MenuController,
        private navCtrl: NavController,
        public loadingController: LoadingController,
        private ratingClient: RatingClient,
        public platform: Platform,
        private router: Router) {
        this.loggedInItems1 = itemsService.loggedInItems1;
        this.loggedInItems2 = itemsService.loggedInItems2;
        this.loggedOutItems = itemsService.loggedOutItems;
        this.loggedOutItems = itemsService.loggedOutItems;
        this.homePageItems = itemsService.homePageItems;
        this.loginItem = itemsService.loginItem;
    }

    ngOnInit() {
       
    }
    async presentLoggedOutToast() {
        let toast = await this.toastCtrl.create({
            message: "You have successfully logged out",
            duration: 3000,
            position: "bottom"
        });

        await toast.present();


    }

    refreshAverageRating(){
        this.loadingAverageRating= true;
        this.ratingClient.average(this.auth.user.uid, EntityTypeEnum.Rating).subscribe((res) => {
            this.auth.averageRating = res;
        }, err => console.log(err)).add(()=>{
            this.loadingAverageRating = false;
        });
    }

    goToSignIn() {
        this.menuCtrl.close("start");
        this.navCtrl.navigateForward("login");
    }

    goToProfile() {
        this.menuCtrl.close("start");
        this.navCtrl.navigateForward("profile");
    }

    closeMainMenu() {
        this.menuCtrl.close("start");
    }

    async logOutConfirmation() {
        let alert = await this.alertCtrl.create({
            header: "Logging out",
            subHeader: "Are you sure you want to logout?",
            buttons: [
                {
                    text: "Yes",
                    handler: async () => {
                        
                        this.menuCtrl.close("start");
                        console.log("Confirm log out");
                        this.navCtrl.navigateRoot("home");
                        await this.auth.signOut();
                        this.navCtrl.navigateRoot("home");
                        await this.presentLoggedOutToast();
                        this.auth.user = null;
                    }
                },
                {
                    text: "No",
                    handler: () => {
                        console.log("Cancel log out");
                    }
                }
            ]
        });
        await alert.present();
    }

    openRootPage(page) {
        this.menuCtrl.close("start");
        this.navCtrl.navigateForward(page);
    }

    isSamePage(pageName: string): boolean {
        if (this.router.url === pageName) {
            return true;
        }
        return false;
    }

    openLogin() {
        this.menuCtrl.close("start");
        this.navCtrl.navigateForward("login");
    }

    async logout() {
        this.logOutConfirmation();
    }

    openPage(page) {
        try {
            this.menuCtrl.close("start");
            this.navCtrl.navigateForward(page);
            
        }
        catch (e) {
            alert("error opening page: " + e);
        }
    }
    

    openExternalPage(url: string){
        window.open(url, "_blank");
    }
}
