import { Component } from '@angular/core';
import { Platform, MenuController, PopoverController, NavController } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { AuthService } from './services/auth.service';
import { PopoverOptions } from '@ionic/core';
import { ProgressService } from './services/progress.service';
import { Router, NavigationEnd, NavigationStart, NavigationError } from '@angular/router';
import { SearchService } from './services/search.service';
import { MenuService } from './services/menu.service';
@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html'
})
export class AppComponent {

  constructor(
    private platform: Platform,
    private splashScreen: SplashScreen,
    private statusBar: StatusBar,
    public menuCtrl: MenuController,
    public menuService: MenuService,
    public auth: AuthService,
    public popoverCtrl: PopoverController,

    public progress: ProgressService,
    private router: Router,
    private searchService: SearchService,
    public navCtrl: NavController
  ) {

    
    this.router.events.subscribe((val) => {
     this.progress.changeProgressColor();
      // see also 
      if (val instanceof NavigationStart) {
        // Show loading indicator
        this.progress.start();
      }

      if (val instanceof NavigationEnd) {
        this.progress.complete();       
      }

      if (val instanceof NavigationError) {
        console.log(val.error);
        this.progress.complete();
      }
    });

    this.initializeApp();

  }

  initializeApp() {

  //  this.loader.preLoadRoute('advert/:id');


    this.platform.ready().then(() => {
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });

  }


  async presentPopover() {
    var popOverOptions: PopoverOptions = {
      // enableBackdropDismiss : false,
      showBackdrop: true,
      cssClass: "backdropOpacityPopover",
      component: "terms-and-conditions"
    }
    var popover = await this.popoverCtrl.create(popOverOptions);
    await popover.present();
  }

  closeMainMenu() {
    this.menuCtrl.close("start");
  }
  closeFilter() {
    this.menuCtrl.close("end");
  }



}
