import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertController, MenuController, ModalController, NavController } from "@ionic/angular";
import { CategoryModel, SearchSortByModel } from '../../clients/bonobogive.client';
import { categories } from "../../constants/categories";
import { ErrorMessageTypeEnum } from "../../enums/ErrorMessageTypeEnum";
import { DropDownOptionModel } from '../../models/DropDownOptionModel';
import { AuthService } from "../../services/auth.service";
import { ErrorService } from "../../services/error.service";
import { GeoService } from "../../services/geo.service";
import { sortByOptions } from "./../../constants/search-sort-by";
import { SearchService } from "./../../services/search.service";

@Component({
    selector: 'app-filter',
    templateUrl: './filter.component.html',
    styleUrls: ['./filter.component.scss']
})
export class FilterComponent implements OnInit {
    ngOnInit(): void {

    }

    userLoggedIn = false;
    categories: CategoryModel[] = [];
    locations: DropDownOptionModel[] = [];
    distance: DropDownOptionModel[] = [];
    sortBy: DropDownOptionModel[] = [];
    showPostcode: boolean = true;
    findingLocation: boolean = false;
    sortByOptions: SearchSortByModel[] = [];
    postcodeLookupSpinner: boolean = false;

    constructor(

        public auth: AuthService,
        public alertCtrl: AlertController,


        public modalCtrl: ModalController,
        private errorService: ErrorService,
        private geoService: GeoService,
        public searchService: SearchService,
        private navController: NavController,

        private router: Router,
        public menuCtrl: MenuController

    ) {
        this.categories = categories;
        this.sortByOptions = sortByOptions;
    }

    isSamePage(pageName: string): boolean {
        if (this.router.url === pageName) {
            return true;
        }
        return false;
    }

    performSearch() {
        this.searchService.search(null, null);
    }

    navigateToSearch() {

        this.menuCtrl.close("end");
        if (this.isSamePage("/search")) {
            //You're already on this page
            console.log("already on search");
            this.searchService.refreshSearchResults();
            this.searchService.search(null, null);
        }
        else {
            this.searchService.search(null, null);
            this.navController.navigateForward("search/false");
        }
    }

    ngAfterViewInit() {
    }

    async getLatLngFromPostcode(postCode: string) {
        this.postcodeLookupSpinner = true;
        try {
            let result = await this.geoService.findAddressByPostcode(postCode);
            this.searchService.searchPageSearchRequest.lat = result?.geometry?.location?.lat();
            this.searchService.searchPageSearchRequest.lng = result?.geometry?.location?.lng();
            this.searchService.searchPageSearchRequest.postcode = postCode;
        }
        catch (e) {
            console.log(e);
        }
      
        this.postcodeLookupSpinner = false;
    }

    async getLocationFromDevice() {
        this.findingLocation = true;

        try {
            await this.geoService.refreshCurrentDevicePosition();
            this.searchService.searchPageSearchRequest.lat = this.geoService.latestDevicePosition.coords.latitude;
            this.searchService.searchPageSearchRequest.lng = this.geoService.latestDevicePosition.coords.longitude;
            let result = await this.geoService.findAddressByCoords(this.geoService.latestDevicePosition.coords.latitude, this.geoService.latestDevicePosition.coords.longitude)
            
            // fix - this will force getLatLngFromPostcode to run because of the onchange event
            this.searchService.searchPageSearchRequest.postcode = this.geoService.extractPostcode(result.address_components);
            this.findingLocation = false;
        }
        catch (e) {
            console.log("no location");
            this.findingLocation = false;
            this.errorService.errorHandler(ErrorMessageTypeEnum.Toast, e, "Unable to find device location");
        }
    }
}
