import { Component, OnInit, Input, ViewChild, ElementRef, Renderer2 } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { SearchService } from '../../services/search.service';
import { Events, Platform, NavController, MenuController, ToastController } from '@ionic/angular';
import { Camera, CameraOptions } from '@ionic-native/camera/ngx';
import { NativeStorage } from '@ionic-native/native-storage/ngx';
import { Router } from '@angular/router';
import { WebSocketNotificationService } from '../../services/websocketnotification.service';
import { MessageEntity, MessageTypeEnum } from '../../clients/bonobogive.client';
import { MenuService } from '../../services/menu.service';
@Component({
  selector: 'app-topbar',
  templateUrl: './topbar.component.html',
  styleUrls: ['./topbar.component.scss']
})
export class TopbarComponent implements OnInit {
  @Input() title: string;
  @Input() displaySearchBar: boolean;
  @Input() displayCameraIcon: boolean;
  isApp: boolean = false;
  isSearchPage: boolean = false;
  placeholder: string = `try "table"`;
  placeholders: string[] = [`Search - it's all free!`];
  constructor(public events: Events,
    public searchService: SearchService,
    public auth: AuthService,

    private camera: Camera,
    public platform: Platform,
    
    private nativeStorage: NativeStorage,
    public menuCtrl: MenuController,
    private router: Router,
    private navCtrl: NavController,
    private toastController: ToastController,
    public menuService: MenuService,
    public webSocketNotificationService: WebSocketNotificationService) {
    if (this.platform.is("desktop")) {
      this.isApp = false;
    } else {
      this.isApp = true;
    }
  }

  @ViewChild('removeFocusInput') removeFocusInput: ElementRef;


  ngOnInit() {

    if (this.isSamePage("/search")) {
      this.isSearchPage = true;
    }
    else {
      this.isSearchPage = false;
    }

    this.randomPlaceHolder();

    // register to notifications

  }



  randomPlaceHolder() {
    var rand = this.placeholders[Math.floor(Math.random() * this.placeholders.length)];
    this.placeholder = rand;
  }

  uploadImage($event): void {
    console.log("image chosen");
    // this.fileService.readImages($event.target, this.advertImages);
  }

  goToPost() {
    this.navCtrl.navigateForward("post");
  }

  openMainMenu() {
    this.menuCtrl.open("start");
  }

  goToIntroduction() {
    this.navCtrl.navigateForward("introduction");
  }

  search(event) {
    event.target.blur();
    if (!this.isSamePage("search")) {
      this.navCtrl.navigateForward("search");
    }

    if (event.target.value) {
      this.searchService.refreshSearchResults();
      this.searchService.searchPageSearchRequest.searchQuery = event.target.value;
      this.searchService.search(null, null);
     // this.searchService.performInitialSearch(query);
    }
  }



  isSamePage(pageName: string): boolean {
    if (this.router.url === pageName) {
      console.log("same page");
      return true;
    }
    return false;
  }




  openNotifications() {

    if (!this.auth || !this.auth.user) {
      this.goToLogin();
      return;
    }

    this.navCtrl.navigateForward("notifications");
  }

  goToRegister() {
    this.navCtrl.navigateForward("register");
  }

  goToLogin() {
    this.navCtrl.navigateForward("login");
  }

  redirectToSearch(ev: any) {

    this.removeFocusInput.nativeElement.focus().
      this.navCtrl.navigateForward("search/true");

  }
  async takePicture(event: any) {

    if (!this.auth || !this.auth.user) {
      this.goToLogin();
      return;
    }

    this.goToPost();
    if (this.platform.is('desktop')) {
      this.goToPost();
      return;
    }

    const options: CameraOptions = {
      quality: 50,
      destinationType: this.camera.DestinationType.DATA_URL,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE,
      targetWidth: 1280,
      targetHeight: 1280,
      saveToPhotoAlbum: false,
    }

    try {
      var imageData = await this.camera.getPicture(options);
      var imageBase64 = "data:image/jpeg;base64," + imageData;
      this.nativeStorage.setItem("latestPicture", imageBase64).then(
        () => console.log('Stored item!'),
        error => console.error('Error storing item', error)
      );
      this.navCtrl.navigateForward("post");
    } catch (e) {
      console.log(e);
    }
  }

  placeHolderFocus(){
    this.placeholder = "try 'table'";
  }

  placeHolderBlur(){
    this.placeholder = "Search - it's all free!";
  }

}
