import { Component, OnInit } from '@angular/core';
import { AuthService } from "./../../services/auth.service";
import {  Input } from "@angular/core";
import { ToastController, AlertController, NavController } from '@ionic/angular';
import { ChatClient, AdvertEntity, UserModel, ChatResponseModel } from '../../clients/bonobogive.client';

@Component({
  selector: 'app-chat-item',
  templateUrl: './chat-item.component.html',
  styleUrls: ['./chat-item.component.scss']
})
export class ChatItemComponent implements OnInit {
  @Input() chat: ChatResponseModel;
    //conversation: ChatResponseModel;
    loaded: boolean = true;


  constructor(
    public auth: AuthService,

    private toast: ToastController,
    private alert: AlertController,
    private navController: NavController,
    private chatClient: ChatClient) { 

    }


async showDeleteToast(){
  const toast = await this.toast.create({
    message: 'Conversation deleted',
    duration: 2000
  });
  toast.present();
}

openConversation(chat: ChatResponseModel) {
  chat.seen = true;
  this.navController.navigateForward(`chat/${chat.chatDetails.primaryKey}`);
}

  async showDeleteConfirmation(chat: ChatResponseModel){
    const alert = await this.alert.create({
      header: 'Delete conversation',
      message: 'Are you sure you want to delete this conversation?',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          cssClass: 'secondary',
          handler: (blah) => {
            
          }
        }, {
          text: 'Delete',
          handler: async () => {
            await this.showDeleteToast();
            this.delete(chat);
          }
        }
      ]
    });

    await alert.present();
  }

  delete(chat: ChatResponseModel){
    this.chatClient.deleteChat(chat.chatDetails.partitionKey, chat.chatDetails.sortKey).subscribe(res=>{
      console.log("chat deleted", res);
    });
  }

  ngOnInit() {
        
  }
}
