import { Pipe, PipeTransform } from '@angular/core';
import { AdvertEntity, FavouriteEntity } from '../clients/bonobogive.client';
import { FavouriteService } from '../services/favourite.service';


@Pipe({
  name: 'favourite',
  pure: false
})
export class FavouritePipe implements PipeTransform {

  constructor(public favouriteService: FavouriteService) {
    
  }

  transform(entityPrimaryKey: string, favouriteAdvertIds: FavouriteEntity[]): boolean {


    if(this.favouriteService.userFavouriteAdvertIds && this.favouriteService.userFavouriteAdvertIds.find(x=>x.entityPrimaryKey === entityPrimaryKey)){
      return true;
    }
    return false;

    // is favourite!
    // if (favouriteAdvertIds.indexOf(advertId) > -1) {
    //     return true;
    // }
    // return false;
}

}
