import { Injectable } from '@angular/core';
import {ErrorMessageTypeEnum} from "../enums/ErrorMessageTypeEnum";
import { AlertController, ToastController } from '@ionic/angular';
@Injectable({
  providedIn: 'root'
})
export class ErrorService {

  constructor(
    public alertCtrl: AlertController,
    public toastCtrl: ToastController
) {

}

errorHandler(messageType: ErrorMessageTypeEnum, err: string, title?: string, subTitle?: string, buttons?: string[]) {

    if (messageType === ErrorMessageTypeEnum.Alert) {
        this.popupAlert(err, title, subTitle, buttons);
    }
    else if (messageType === ErrorMessageTypeEnum.Toast) {
        this.toastAlert(err, title, subTitle, buttons);
    }
    else {
        console.log(JSON.stringify(err));
    }

}

private async toastAlert(err: string, title?: string, message?: string, buttons?: string[]) {

    let fullMessage: string;

    if(title && message)
    {
        fullMessage = title + ": " + message;
    }
    else if(title)
    {
        fullMessage = title;
    }
    else if(message)
    {
        fullMessage = message;
    }
    else
    {
        fullMessage = err;
    }

    const  toast = await this.toastCtrl.create({
      message: fullMessage,
      duration: 3000,
      position: "bottom",
      showCloseButton: true
    });

    await toast.present();
    console.log(JSON.stringify(err));
}

private async popupAlert(err: string, title?: string, message?: string, buttons?: string[]) {
    let alert = await this.alertCtrl.create({
        header: title ? title : "Error",
        message: message ? message : "<p>We apologise, something has gone wrong.</p> <p><i><small>" + err + "</small></i></p>",
        buttons: buttons ? buttons : ["OK"]
    });
    await alert.present();
    console.log(JSON.stringify(err));
}
}
