import { AuthService } from './auth.service';

import { AdvertService } from "./advert.service";
import { Injectable } from "@angular/core";
import { AdvertEntity, AdvertClient, FavouriteEntity, FileResponse, PaginationRequest } from '../clients/bonobogive.client';
import { throwError, Observable } from 'rxjs';
import { tap } from 'rxjs/operators';



@Injectable({
    providedIn: 'root'
})
export class FavouriteService {

    userFavouriteAdvertIds: FavouriteEntity[] = [];

    constructor(
        public advertService: AdvertService,
        public auth: AuthService,
        private advertClient: AdvertClient) {
        this.userFavourites();
    }

    userFavourites() {
        if (!this.auth || !this.auth.user || !this.auth.user.uid) return;

        var pagination = new PaginationRequest();
        pagination.nextRequestBlob = undefined;
        pagination.take = 1000;

        this.advertClient.favouritesAdverts(this.auth.user.uid, pagination).subscribe((res) => {
            this.userFavouriteAdvertIds = res.entities;
        });
    }

    async addFavourite(entityPrimaryKey: string) {

        var tempFavouriteKey = entityPrimaryKey + this.auth.user.uid;

        var tempFavEntity = new FavouriteEntity();
        tempFavEntity.primaryKey = tempFavouriteKey;
        tempFavEntity.entityPrimaryKey = entityPrimaryKey;
        this.userFavouriteAdvertIds.push(tempFavEntity);

        this.advertClient.addFavourite(entityPrimaryKey, this.auth.user.uid).subscribe(res => {
            var index = this.userFavouriteAdvertIds.findIndex(x => x.entityPrimaryKey === res.entityPrimaryKey);
            if (index === -1) {
                console.log("Couldn't find array to update")
                return;
            }
            this.userFavouriteAdvertIds[index] = res;
            console.log(`Advert added ${res}`)
        });
    }

    // async deleteFavourite(entityPrimaryKey: string): Promise<void> {

    //     var favourites = this.userFavouriteAdvertIds.filter(x=>x.entityPrimaryKey === entityPrimaryKey);
    //     if(favourites.length === 0){
    //         console.log("Favourite id not found");
    //         return;
    //     }

    //     this.userFavouriteAdvertIds = this.userFavouriteAdvertIds.filter(x=> x.entityPrimaryKey !== entityPrimaryKey);

    //     this.advertClient.deleteFavourite(partitionKey, sortKey).subscribe(()=>{
    //         console.log("Favourite deleted");
    //     });

    // }

    deleteFavouriteByAdvertEntity(advert: AdvertEntity) : Observable<FileResponse> {

        var result = this.advertClient.deleteFavourite(this.auth.user.uid, advert.primaryKey)
            .pipe(tap(x => {
                this.userFavouriteAdvertIds = this.userFavouriteAdvertIds.filter(x => x.entityPrimaryKey !== advert.primaryKey);
                console.log(`Favourite for advert ${advert.primaryKey} deleted`);
            }));
            return result;
    }


}
