import { Pipe, PipeTransform } from '@angular/core';
import { ChatResponseModel } from '../clients/bonobogive.client';

@Pipe({
  name: 'conversationsSeenCount',
  pure: false
})
export class ConversationsSeenCountPipe implements PipeTransform {

  transform(value: ChatResponseModel[], args?: any): any {
    return value.map(x=>x.seen).filter(x=>x === true).length;
  }

}
