

import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Geolocation, Geoposition } from "@ionic-native/geolocation/ngx";
import { AlertController, Platform, ToastController } from "@ionic/angular";

@Injectable({
    providedIn: 'root'
})
export class GeoService {

    latestDevicePosition: Geoposition;

    constructor(
        private toast: ToastController,
        public http: HttpClient,
        private geolocation: Geolocation,
        private platform: Platform,
        private alertController: AlertController
    ) {

    }

    async presentAlert() {
        const alert = await this.alertController.create({
            header: 'Location error',
            subHeader: 'User denied',
            message: 'You have denied Bonobogive access to your location. To allow Bonobogive access to your location, please enable it on your device.',
            buttons: ['OK']
        });

        await alert.present();
    }

    async presenToast(message: string) {
        let toast = await this.toast.create({
            message: message,
            duration: 5000,
            position: "bottom"
        });

        await toast.present();
    }

    extractPostcode(locations: google.maps.GeocoderAddressComponent[]): string {
        var result = "";
        if (!locations || locations.length === 0) {
            return result;
        }

        for (var address of locations) {
            if (address.types[0] === "postal_code") {
                result = address.long_name;
            }
        }

        return result;
    }

    async findAddressByCoords(lat: number, lng: number): Promise<google.maps.GeocoderResult> {
        let results = await this.searchAddressesByCoords(lat, lng);

        if (results?.length < 1) return null;

        return results[0];
    }


    searchAddressesByCoords(lat: number, lng: number): Promise<google.maps.GeocoderResult[]> {

        if (!lat) lat = 0;
        if (!lng) lng = 0;

        return new Promise<google.maps.GeocoderResult[]>(resolve => {

            var geocoder = new google.maps.Geocoder();

            geocoder.geocode({ location: { lat: lat, lng: lng } }, (results, status) => {

                //if (status !== 'OK') { console.log(`Failed to find addresses for lat lng ${lat},${lng}`); return []; }

                if (results?.length < 1) { console.log(`no addresses found for lat lng ${lat},${lng}`); throw `no address found for postcode ${lat},${lng}`; };

                resolve(results);

            });
        });
    }

    async findAddressByPostcode(postcode: string): Promise<google.maps.GeocoderResult> {

        let results = await this.searchAddressesByPostcode(postcode);

        if (results?.length < 1) return null;

        return results[0];

    }

    searchAddressesByPostcode(postcode: string): Promise<google.maps.GeocoderResult[]> {

        return new Promise<google.maps.GeocoderResult[]>(resolve => {

            var geocoder = new google.maps.Geocoder();

            geocoder.geocode({ 'address': postcode }, (results, status) => {

                //if (status !== 'OK') { console.log(`Failed to find address ${status}`); return []; }

                if (results?.length < 1) {
                    console.log(`no address found for postcode ${postcode}`);
                    throw `no address found for postcode ${postcode}`;
                };

                resolve(results);

            });

        });
    }


    async refreshCurrentDevicePosition(): Promise<void> {
        console.log("Refreshing current position...");
        try {
            this.latestDevicePosition = await this.geolocation.getCurrentPosition();
            console.log("Found device current position", this.latestDevicePosition)
        }
        catch (ex) {
            this.latestDevicePosition = null;
            if (ex.message === "User denied Geolocation" || ex.PERMISSION_DENIED) {
                await this.presentAlert();
            } else {
                await this.presenToast("Failed to load location");
            }
            throw ex;

        }
    }
}
