import { Component, OnInit, Input } from '@angular/core';
import { AlertController } from '@ionic/angular';

@Component({
  selector: 'app-rating',
  templateUrl: './rating.component.html',
  styleUrls: ['./rating.component.scss']
})
export class RatingComponent implements OnInit {
  @Input('rating') set rating(value: number) {
    this._rating = value;
    this.calculateLevel(this.rating, this.count);
  }

  @Input("showHelp") showHelp: boolean = true;
  @Input("large") large: boolean = false;
  @Input("centered") centered: boolean = false;
  @Input("showBadge") showBadge: boolean;
  @Input("showStars") showStars: boolean;
  @Input("readonly") readonly: boolean;
  @Input('count') set count(value: number) {
    this._count = value;
    this.calculateLevel(this.rating, this.count);
  }

  _count: number = 0;
  get count(): number {
    return this._count;
  }

  _rating: number = 0;
  get rating(): number {
    return this._rating;
  }

  levelText = "";
  level = 0;

  constructor(public alertController: AlertController) { }

  ngOnInit() {
  }


  calculateLevel(averageScore, reviewCount) {

    if (reviewCount >= 100 && averageScore >= 4) {
      this.level = 100;
      this.levelText = "master";
      return;
    }

    if (reviewCount >= 50 && averageScore >= 3) {
      this.level = 50;
      this.levelText = "trusted";
      return;
    }

    this.levelText = "beginner";
    this.level = 0;
    return;
  }

  async showHelpPopup() {
    const alert = await this.alertController.create({
      header: 'Levels',
      subHeader: 'How are the calculated?',
      message: 'A beginner has less than 50 reviews from other users. A trusted has more than 50 reviews with an average rating of 3 or more. A Master has more than 100 reviews with an average of 4 or more',
      buttons: ['OK']
    });

    await alert.present();
  }

}
