import { Component, Input, OnInit } from '@angular/core';
import { AlertController, Events, NavController, ToastController } from '@ionic/angular';
import { AdvertClient, AdvertEntity } from '../../clients/bonobogive.client';
import { AdvertService } from '../../services/advert.service';
import { AuthService } from '../../services/auth.service';
import { FavouriteService } from '../../services/favourite.service';
import { SearchService } from '../../services/search.service';

@Component({
  selector: 'app-adverts',
  templateUrl: './adverts.component.html',
  styleUrls: ['./adverts.component.scss']
})


export class AdvertsComponent implements OnInit {

  @Input('adverts')
  set adverts(value: AdvertEntity[]) {
    this._adverts = value;
  }
  _adverts: AdvertEntity[];
  get adverts(): AdvertEntity[] {
    return this._adverts;
  }

  ngOnInit() {
    
  }


  @Input("showDelete") showDelete: boolean = false;
  @Input() enableFavourites: boolean;

  imageIndex: number = 0;
  noImage: boolean = true;
  containImage: boolean = true;
  constructor(public events: Events,
    // public app: App,
    public searchService: SearchService,
    public auth: AuthService,
    private navController: NavController,
    public favourtieService: FavouriteService,
    public alertController: AlertController,
    public advertService: AdvertService,
    public toastController: ToastController,
    public alertCtrl: AlertController,
    private advertClient: AdvertClient) { }

  async presentAdvertDeletedToast(advert: AdvertEntity) {
    const toast = await this.toastController.create({
      message: 'Advert ' + advert.title + " deleted",
      duration: 2000
    });
    toast.present();
  }

  async presentAlertConfirm(advert: AdvertEntity) {
    const alert = await this.alertController.create({
      header: 'Confirm',
      message: 'Are you sure you want to delete this advert?',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          cssClass: 'secondary',
          handler: async () => {

          }
        }, {
          text: 'Yes',
          handler: async () => {
            await this.deleteAdvertConfirm(advert);
            await this.presentAdvertDeletedToast(advert);
          }
        }
      ]
    });

    await alert.present();
  }

  async deleteAdvertConfirm(advert: AdvertEntity) {
    await this.advertClient.delete(advert.partitionKey, advert.sortKey);
    console.log("advert " + advert.primaryKey + " deleted");
  }


  loadAdvert(advert: AdvertEntity) {
    if (advert.draft) {
      this.navController.navigateForward("post/" + advert.primaryKey);
    }
    else {
      this.navController.navigateForward("advert/" + advert.primaryKey);
    }
  }


  async showDeleteFavouriteConfirm(advert: AdvertEntity) {
    let confirm = await this.alertCtrl.create({
      header: "Delete favourite?",
      message: "Are you sure you want to delete this favourite?",
      buttons: [
        {
          text: "Yes",
          handler: () => {
            this.deleteFavourite(advert);
          }
        },
        {
          text: "No",
          handler: () => {
            console.log("Agree clicked");
          }
        }
      ]
    });
    await confirm.present();
  }

  deleteFavourite(advert: AdvertEntity) {
    if(!this.auth || !this.auth.user){
      this.navController.navigateForward("login");
      return;
    }

    this.favourtieService.deleteFavouriteByAdvertEntity(advert).subscribe(res => {
      
    }, err => {
      console.log(err);
    });;
}

  addFavourite(advert: AdvertEntity) {

    if(!this.auth || !this.auth.user){
      this.navController.navigateForward("login");
      return;
    }

    if(!advert) {
      return;
    }

    this.favourtieService.addFavourite(advert.primaryKey);
  }


}
