import { Component, OnInit } from '@angular/core';
import { Input } from "@angular/core";
import { NavController, NavParams, ModalController } from "@ionic/angular";
import { RatingChangeEvent, ClickEvent } from 'angular-star-rating';
import { RatingEntity, IRatingEntity, RatingBreakdown, RatingClient, CreateRatingRequest, EntityTypeEnum, AverageRatingEntity } from '../../clients/bonobogive.client';
import { Observable } from 'rxjs';


@Component({
  selector: 'app-rate',
  templateUrl: './rate.component.html',
  styleUrls: ['./rate.component.scss']
})
export class RateComponent implements OnInit {
  @Input('ratedUserId') ratedUserId: string;
  @Input('advertId') advertPrimaryKey: string;
  @Input('showRatingsCount') showRatingsCount: boolean;
  @Input('reviewerUserId') ratingUserId: string;
  @Input('getReviewerRating') getReviewerRating: boolean;
  @Input('getAverageUserRating') getAverageUserRating: boolean;
  @Input('readOnlyRating') readOnlyRating: boolean;
  rating: RatingEntity;
  averageRating: AverageRatingEntity;
  ratingComplete: boolean = false;

  foundPreviousRating = false;

  constructor(
    private navController: NavController,
    private ratingClient: RatingClient) {
    this.rating = new RatingEntity();
    this.rating.ratingBreakdown = new RatingBreakdown();
  }

  ratingChange(event : ClickEvent, ratingType) {
    if(! this.rating.ratingBreakdown)  this.rating.ratingBreakdown = new RatingBreakdown();
    switch (ratingType) {
      case "gratefulness": {
        this.rating.ratingBreakdown.gratefulness = event.rating;
        break;
      }

      case "punctuality": {
        this.rating.ratingBreakdown.punctuality = event.rating;
        break;
      }

      case "communication": {
        this.rating.ratingBreakdown.communication = event.rating;
        break;
      }

      case "wouldUseAgain": {
        this.rating.ratingBreakdown.wouldUseAgain = event.rating;
        break;
      }
      default:  break;

    }

    this.calculateRatingComplete();

    this.calculateaverage();
   
  }

  private calculateRatingComplete() {
    if(
      this.rating.ratingBreakdown.gratefulness > 0 &&
      this.rating.ratingBreakdown.punctuality > 0 &&
      this.rating.ratingBreakdown.communication > 0 &&
      this.rating.ratingBreakdown.wouldUseAgain > 0
      ){
        this.ratingComplete = true;
      }
      else{
        this.ratingComplete = false;
      }
  }

  ngOnInit() {
    if (this.getAverageUserRating) {
      this.getOverallAverageUserRating();
    }
    else {
      if (!this.ratedUserId || !this.ratingUserId) {
        // this.navController.navigateRoot("home");
        return;
      }
      this.getReviewersPreviousRating();
    }
  }

  createRatingRequest(): CreateRatingRequest {
    const request: CreateRatingRequest = new CreateRatingRequest();
    request.entityType = EntityTypeEnum.Rating;
    request.ratedUserId = this.ratedUserId;
    request.ratingUserId = this.ratingUserId;
    request.entityPrimaryKey = this.advertPrimaryKey;
    request.ratingBreakdown = new RatingBreakdown();
    request.ratingBreakdown.communication = this.rating.ratingBreakdown.communication;
    request.ratingBreakdown.gratefulness = this.rating.ratingBreakdown.gratefulness;
    request.ratingBreakdown.punctuality = this.rating.ratingBreakdown.punctuality;
    request.ratingBreakdown.wouldUseAgain = this.rating.ratingBreakdown.wouldUseAgain;
    return request;
  }

  rateUser(): Observable<RatingEntity> {
   var request = this.createRatingRequest();
    return this.ratingClient.post(request);
  }

  getOverallAverageUserRating() {
    console.log("getOverallAverageUserRating");
    this.ratingClient.average(this.ratedUserId, EntityTypeEnum.Advert).subscribe(res => {
      console.log("getOverallAverageUserRating", res);
      this.rating.ratingBreakdown.gratefulness = res.averageRatingBreakdown.gratefulness;
      this.rating.ratingBreakdown.punctuality = res.averageRatingBreakdown.punctuality;
      this.rating.ratingBreakdown.communication = res.averageRatingBreakdown.communication;
      this.rating.ratingBreakdown.wouldUseAgain = res.averageRatingBreakdown.wouldUseAgain;
      this.rating.breakdownAverageRating = res.averageRating;
    });
  }

  getReviewersPreviousRating() {
    console.log("getReviewersPreviousRating");
    this.ratingClient.usersRatingOfUser(this.ratedUserId, this.ratingUserId, EntityTypeEnum.Advert, this.advertPrimaryKey).subscribe(res => {
      console.log("getReviewersPreviousRating", res);
      if (res) {
        this.foundPreviousRating = true;
        this.rating.ratingBreakdown.gratefulness = res.ratingBreakdown.gratefulness;
        this.rating.ratingBreakdown.punctuality = res.ratingBreakdown.punctuality;
        this.rating.ratingBreakdown.communication = res.ratingBreakdown.communication;
        this.rating.ratingBreakdown.wouldUseAgain = res.ratingBreakdown.wouldUseAgain;
        this.calculateaverage();
        this.calculateRatingComplete();
      }
    });
  }


  private calculateaverage() {
    this.rating.breakdownAverageRating =
      (
        (this.rating.ratingBreakdown.wouldUseAgain ? this.rating.ratingBreakdown.wouldUseAgain : 0) +
        (this.rating.ratingBreakdown.punctuality ? this.rating.ratingBreakdown.punctuality : 0) +
        (this.rating.ratingBreakdown.gratefulness ? this.rating.ratingBreakdown.gratefulness : 0) +
        (this.rating.ratingBreakdown.communication ? this.rating.ratingBreakdown.communication : 0)
      ) / 4;
  }
}
